.wave-gray {
  width: 100vw;
  height: 18vw;
  background: url('./Components/image/wave-gray.svg');
  background-size: cover;
  position: absolute;
  bottom: 0;
}

.wave-black {
  width: 100vw;
  height: 18vw;
  background: url('./Components/image/wave-black.svg');
  background-size: cover;
  position: absolute;
  bottom: 0;
}

.wave-wrapper {
  width: 100vw;
  height: 20vw;
  position: relative;
}

.wave-bg-gray { background-color: #3A3A3A; }
.wave-bg-black { background-color: #000000; }