.animateError {
    display: inline-block;
    border: 1px solid red;
    border-radius: 50%;
    padding: 10px 15px;
    font-family: Arial;
    animation: errorRotate 5s linear infinite;
}

@keyframes errorRotate {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}